import React from "react";

const Trademark = () =>{
    const footerStyle={
        width: "100%"
    }
    const pStyle = {
        textAlign:"right",
        color: "deeppink",
        fontFamily: "Arial",
        fontWeight: "bold",
        margin:"1%"
      };
return <footer style={footerStyle} className="trademark"><p style={pStyle}>KATRIN SALAC © 2019</p></footer>
};

export default Trademark;



