import React,{useState,useEffect} from "react";
import { Link } from "react-router-dom";

import codepen from "../images/codepen.png";
import github from "../images/github.png";
import linkedin from "../images/linkedin.png";

const NavTop= () =>{
const [color,setColor]=useState(null);
useEffect(()=>{
  console.log(color)
  function listenScrollEvent(e) {
    if (window.scrollY > 100) {
      setColor("grey")
    } else {
      setColor("transparent")
    }
  }
  window.addEventListener('scroll', listenScrollEvent())
  })


return(
  <React.Fragment>
    <nav className="navTop" id="navTop" style={{backgroundColor: color}}>
      <Link to="/">
        <i className="back-arrow"></i>
      </Link>
          <a href="https://codepen.io/KasaBerlin" target="blank">
          <img src={codepen} alt="codepen"/>
          </a>
          <a href="https://www.linkedin.com/in/katrin-salac" target="blank">
             <img src={linkedin} alt="linkedin"/>
          </a>
          <a href="https://github.com/KasaBerlin" target="blank">
             <img src={github} alt="github"/>
          </a>
      </nav>
    </React.Fragment>
    )};

export default NavTop;
